.header-container {
  position: relative;
  padding-top: 0px;
  background-image: url(../images/slider1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 580px;
  padding-top: 208px;
  padding-bottom: 0px;
}

.logo-icon {
  position: absolute;
  top: 40px;
  left: 31px;
  transform: translateX(50%);
  fill: white;
}

.menu-button {
  position: absolute;
  top: 60px;
  right: 39px;
  z-index: 2;
  transform: translateX(-50%);
  display: inline-flex;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;

  .menu-close {
    display: none;
  }
}

.menu-button.is-open .menu-close {
  display: block;
}

.menu-button.is-open .menu-icon {
  display: none;
}

.main-menu {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 70%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  z-index: 1;
  padding-top: 118px;
  padding-left: 40px;

  nav {
    margin-bottom: 64px;
  }
  address {
    display: inline-flex;
    flex-direction: column;
    margin-bottom: 64px;

    a {
      margin-bottom: 24px;

      font-weight: 600;
      font-size: 18px;
      font-style: normal;
      line-height: 1.38;
      letter-spacing: 0.1em;
      text-decoration: none;
      color: $primary-text-color;
    }
  }
}
.is-open {
  opacity: 1;
  pointer-events: all;
}

.menu-list {
  color: $primary-text-color;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.1em;
  list-style: none;

  a {
    color: inherit;
    text-decoration: none;
  }
}

.soc-networks {
  color: $primary-text-color;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.2em;
  list-style: none;
  margin: 0px;
  padding: 0px;

  a {
    color: inherit;
    text-decoration: none;
  }
  li:last-child {
    margin-left: 100px;
  }
}

.soc-list {
  display: inline-flex;
  align-items: center;
  margin-bottom: 25px;

  &::before {
    content: '';
    width: 60px;
    height: 1px;
    background-color: $primary-text-color;
    margin-right: 40px;
  }
}

.hero {
  h1 {
    color: rgba(157, 164, 189, 0.6);
    font-size: 11px;
    font-weight: 600;
    line-height: 1.36;
    letter-spacing: 0.1em;
    display: inline-flex;
    align-items: center;
    margin-top: 0px;
    margin-bottom: 20px;

    &::before {
      content: '';
      width: 60px;
      height: 1px;
      background-color: rgba(157, 164, 189, 0.6);
      margin-right: 20px;
    }
  }

  h2 {
    margin-top: 0px;
    margin-bottom: 20px;
    color: white;
    font-family: $font-title;
    font-size: 48px;
    font-weight: 700;
    line-height: 1.16;
    letter-spacing: 0.05em;
  }

  p {
    margin-top: 0px;
    margin-bottom: 0px;
    color: $additional-text-color;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.62;
    letter-spacing: 0.02em;
  }
}

.back,
.forward {
  opacity: 0;
  pointer-events: none;
}
